.slider {
    @include invis;
    @include sm {
        margin-left: 10px; }
    &:hover {
        .btn-slider {
            @include vis; } }
    &__slide {
        width: 700px !important;
        padding-bottom: 350px;
        background-color: $light-bg;
        @include cover;
        border-radius: $radiusBig;
        position: relative;
        @include md {
            width: 653px !important;
            padding-bottom: 326px; }
        @include sm {
            width: 285px !important;
            padding-bottom: 168px; } }
    .slick-slide {
        margin: 0 12px;
        @include sm {
            margin: 0 5px; } }
    .btn-slider {
        @include invis;
        &_prev {
            left: calc(50% - 480px); }
        &_next {
            right: calc(50% - 480px); } }
    &__text-first {
        font-size: 25px;
        line-height: 1.2;
        font-weight: 700;
        position: absolute;
        left: 40px;
        top: 40px;
        @include sm {
            left: 10px;
            top: 10px;
            font-size: 15px; } }
    &__text-second {
        position: absolute;
        top: 70px;
        right: 50px;
        @include sm {
            top: 15px;
            right: 15px; }
        h2 {
            font-size: 60px;
            font-weight: 700;
            line-height: 1;
            margin-bottom: 40px;
            @include sm {
                font-size: 20px;
                margin-bottom: 16px; } }
        p {
            font-size: 20px;
            line-height: 1.25;
            @include sm {
                font-size: 15px; } } }
    &__text-third {
        position: absolute;
        left: 40px;
        bottom: 40px;
        @include sm {
            left: 20px;
            bottom: 10px; }
        p {
            font-size: 20px;
            line-height: 1.25;
            @include sm {
                font-size: 15px; } }
        h2 {
            font-size: 50px;
            line-height: 1;
            @include sm {
                font-size: 24px; } } } }

.slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%); }


.slider-single {
    @include invis;
    position: relative;
    &:hover {
        .btn-single-slider {
            @include vis; } }
    @include sm {
        overflow: visible;
        padding-left: 11px;
        padding-right: 21px;
        .slick-list {
            overflow: visible; } }
    &__slide {
        padding-bottom: 630px;
        @include cover;
        border-radius: $radiusBig;
        @include md {
            padding-bottom: 374px; }
        @include sm {
            width: 270px !important;
            padding-bottom: 310px; } }
    .btn-single-slider {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        @include invis;
        transition: visibility $tr;
        @include sm {
            display: none !important; }
        &_prev {
            left: 20px; }
        &_next {
            right: 20px; } }
    .slider-wrapper {
        transform: translate3d(0,0,0); }
    &__thumb {
        @include cover;
        padding-bottom: calc(100% / 6 + 3px);
        width: calc(100% / 6 + 3px);
        border-radius: $radius; } }

.slider-single-thumbs-wrapper {
    position: relative;
    margin-top: 16px;
    @include sm {
        display: none; }
    .btn-slider-thumbs {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 100;
        transform-origin: center;
        &_prev {
            left: 25px;
            @include md {
                left: 0; } }
        &_next {
            right: 25px;
            @include md {
                right: 0; } }
        @include sm {
            display: none; } } }

.slider-single-thumbs {
    position: relative;

    &.swiper-container {
        width: calc(100% - 160px);
        border-radius: $radius;
        @include md {
            width: calc(100% - 80px); } }
    // .swiper-wrapper
    //     padding-left: 10%
    //     padding-right: 10%
    .swiper-wrapper {
        // // transform: translate3d(50px,0,0) !important

 }        // overflow: hidden
    .swiper-slide-thumb-active {
        opacity: 0.5; } }


.swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 5px;
    height: 10px;
    width: 10px; }

.swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet {
    opacity: 0.5;
    background-color: $white; }

.swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet-active {
    opacity: 1; }


.swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
    bottom: 25px; }

.cards-slider {
    margin: 0 -14px 26px;
    display: flex;
    @include md {
        margin: 0 -12px 26px !important;
        padding-right: 27px; }
    @include sm {
        margin: 0 -5px 20px !important;
        padding-right: 10px; }
    &.swiper-wrapper {
        box-sizing: border-box; }
    .swiper-slide {
        margin: 0 14px;
        @include md {
            margin: 0 12px;
            width: 300px !important; }
        @include r(900) {
            width: 266px !important; }
        @include sm {
            margin: 0 5px; }
        @include r(500) {
            width: 219px !important; } }
    .swiper-slide {
        flex-shrink: inherit; } }


.cards-slider-container {
    margin: 0 auto;
    padding: 0 27px;
    max-width: 1014px;
    @include md {
        padding: 0 0 0 27px;
        max-width: inherit; }
    @include sm {
        padding: 0 0 0 10px; } }

.swiper-container {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    list-style: none;
    z-index: 1; }

