//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$SanFrancisko: 'San Francisco';
$rouble: 'PTRouble';

@font-face {
	@include fontdefault(normal, 500, $rouble);
	@include fontpath('PTRoubleSans', 'PTRoubleSans', PTRoubleSans); }

@font-face {
	@include fontdefault(normal, 700, $SanFrancisko);
	@include fontpath('SFUIText-Bold', 'SFUIText-Bold', SFUIText-Bold); }
@font-face {
	@include fontdefault(normal, 600, $SanFrancisko);
	@include fontpath('SFUIText-Semibold', 'SFUIText-Semibold', SFUIText-Semibold); }
@font-face {
	@include fontdefault(normal, 500, $SanFrancisko);
	@include fontpath('SFUIText-Medium', 'SFUIText-Medium', SFUIText-Medium); }
@font-face {
	@include fontdefault(normal, 400, $SanFrancisko);
	@include fontpath('SFUIDisplay-Regular', 'SFUIDisplay-Regular', SFUIDisplay-Regular); }
