.btn {
    display: inline-block;
    font-family: $SanFrancisko;
    font-weight: 600;
    font-size: 15px;
    text-align: center;
    background: none;
    user-select: none;
    vertical-align: middle;
    line-height: 1.2;
    color: $black;
    &_blue {
        background-color: $blue;
        color: $white;
        transition: background-color 0.4s;
        &:hover {
            background-color: $blueHovered; }
        &:active {
            background-color: $blueClicked; } }
    &_light {
        background-color: $light-button-bg;
        color: $blue; }
    &_grey {
        background-color: $light-bg;
        color: $black;
        transition: background-color 0.4s;
        &:hover {
            background-color: $light-bgHovered; }
        &:active {
            background-color: $light-bgClicked; } }
    &_dark-grey {
        background-color: $mainGrey;
        transition: background-color 0.4s;
        &:hover {
            background-color: #a0a0a0; }
        &:active {
            background-color: #969696; } }
    &_light-grey {
        background-color: $light-bg;
        color: $grey;
        &:hover {
            background-color: $light-bgHovered; }
        &:active {
            background-color: $light-bgClicked; } }
    &_green {
        background-color: $green; }
    &_white {
        background-color: $white; }
    &_big {
        width: 230px;
        padding: 15px 16px;
        border-radius: 25px;
        line-height: 1.3; }
    &_square {
        border-radius: $radius;
        padding: 11px 14px;
        width: 177px;
        @include sm {
            width: 100%; } }
    &_small {
        font-size: 14px;
        padding: 10px 21px;
        border-radius: 18px;
        // +sm
 }        //     padding: 10px 10px 9px
    &_middle {
        border-radius: 20px;
        font-size: 15px;
        padding: 11px 20px; }
    &_middle-wide {
        border-radius: 20px;
        font-size: 15px;
        padding: 11px 30px; }
    &_link {
        color: $grey;
        transition: color $tr;
        &:hover {
            color: $black; } }
    &_link-collection {
        padding: 12px 16px;
        margin-bottom: 9px; }
    &_small-link {
        font-size: 13px;
        padding: 12px 14px;
        font-weight: 600; }
    &_check-main {
        padding: 9px 20px;
        background: $white;
        border-radius: 18px;
        line-height: 35px; }
    &_upper {
        text-transform: uppercase; }
    &_text-wide {
        letter-spacing: 0.03em; }
    &_text {
        font-size: 15px;
        vertical-align: top;
        margin-left: 7px; }
    &__text {
        font-size: 15px;
        vertical-align: top; }
    &_collection-add {
        font-size: 0;
        .icon {
            font-size: 17px;
            line-height: 1; } }
    &_text-white {
        color: $white; }
    &_like {
      font-size: 0;
      .icon {
        font-size: 15px;
        margin-right: 6px;
        margin-top: 1px; } }
    &_save-card {
      min-width: 120px;
      font-size: 0;
      padding: 10px;
      border-radius: 18px;
      .btn__text {
        font-size: 12px;
        margin-left: 6px; }
      .icon {
          vertical-align: middle;
          font-size: 15px;
          line-height: 1; } } }

.btn-watch {
    .icon {
        font-size: 12px;
        margin-left: 1px; } }

.btn-user {
    @include cover; }


// =========== CIRCLE ==============

.btn-circle {
    display: inline-block;
    @include circle(36);
    line-height: 36px;
    font-size: 0;
    text-align: center;
    vertical-align: middle;
    &_big {
        @include circle(40);
        line-height: 40px; }
    .icon {
        font-size: 16px;
        vertical-align: middle;
        display: inline-block;
        line-height: 1; } }


.btn-delete {
    background-color: $white;
    &_dark {
        background-color: $light-bg;
        &:hover {
            background-color: $light-bgHovered; }
        &:active {
            background-color: $light-bgClicked; } }
    .icon {
        font-size: 12px;
        color: $grey; } }

.btn-to-top {
    background-color: rgba($black, 0.3);
    .icon {
        font-size: 8px;
        color: $white; } }

.btn-checked {
    background-color: $white;
    color: $blue;
    @include circle(30);
    line-height: 30px;
    &_inverted {
        background-color: $blue;
        color: $white; }
    .icon {
        font-size: 10px; } }

.btn-add,
.btn-prev,
.btn-menu,
.btn-share {
    background-color: $light-bg;
    color: $grey;
    &:hover {
        background-color: $light-bgHovered; }
    &:active {
        background-color: $light-bgClicked; } }

.btn-add {
    cursor: pointer;
    &_blue {
        height: 40px;
        width: 40px;
        background-color: $blue;
        color: $white;
        line-height: 40px;
        &:hover {
            background-color: $blueHovered; }
        &:active {
            background-color: $blueClicked; } }
    &_dark {
        color: $white;
        background-color: #d8d8d8; } }

.btn-next {
    color: $blue;
    background-color: $light-blue;
    .is-active & {
        color: $white;
        background-color: $blue; } }



.btn-up {
    color: $white;
    background-color: $blue;
    .is-active & {
        color: $blue;
        background-color: $light-blue; } }

.btn-check {
    background-color: $white;
    .icon {
        font-size: 10px;
        color: $blue; } }

.btn-menu {
    position: relative;
    .icon {
        font-size: 4px;
        @include vcenter; } }

.btn-share {
    .icon {
        font-size: 14px;
        margin-bottom: 1px; } }

.btn-message-mobile {
    .icon {
        font-size: 20px; } }

.btn-slider {
    @include circle(40);
    font-size: 0;
    text-align: center;
    vertical-align: middle;
    background-color: rgba($black, 0.3);
    color: $white;
    z-index: 50;
    &_prev .icon {
        margin-right: 2px; }
    &_next .icon {
        margin-left: 2px; }
    &:hover {
        background-color: rgba($black, 0.7); }
    .icon {
        font-size: 15px;
        vertical-align: middle; } }

.btn-slider-thumbs {
    @include circle(30);
    font-size: 0;
    text-align: center;
    vertical-align: middle;
    background-color: $light-bg;
    color: $white;
    z-index: 50;
    transition: background-color $tr;
    &_prev .icon {
        margin-right: 2px; }
    &_next .icon {
        margin-left: 2px; }
    &:hover {
        background-color:  $light-bgHovered; }
    .icon {
        font-size: 10px;
        color: $grey;
        vertical-align: middle; } }

