// =============== SOCIAL ===============

.btn-social {
    width: 290px;
    font-family: $SanFrancisko;
    padding: 16px 16px;
    border-radius: 25px;
    line-height: 1.3;
    font-size: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    word-spacing: 0.2em;
    font-weight: 600;
    @include sm {
        width: 250px; }
    .icon,
    img {
        margin-right: 12px; } }

.btn-fb {
    background-color: $fb;
    color: $white;
    &:hover {
        background-color: darken($fb, 5%); }
    &:active {
        background-color: darken($fb, 10%); }
    .icon {
        font-size: 20px; } }
.btn-vk {
    background-color: $vk;
    color: $white;
    &:hover {
        background-color: darken($vk, 5%); }
    &:active {
        background-color: darken($vk, 10%); }
    .icon {
        font-size: 12px; } }

.btn-google {
    background-color: transparent;
    border: 1px solid $extra-light-grey; }

// ================ SOCIAL-CIRCLE ===================

.btn-social-rounded {
    @include circle(50);
    color: $white;
    font-size: 0;
    display: inline-block;
    vertical-align: middle;
    &.btn-vk {
        .icon {
            font-size: 14px; } }
    &.btn-twitter {
        background-color: $twitter;
        &:hover {
            background-color: darken($twitter, 5%); }
        &:active {
            background-color: darken($twitter, 10%); }
        .icon {
            font-size: 17px; } }
    &.btn-pinterest {
        background-color: $pinterest;
        &:hover {
            background-color: darken($pinterest, 5%); }
        &:active {
            background-color: darken($pinterest, 10%); }
        .icon {
            font-size: 26px; } }
    &.btn-messanger {
        background-color: $messanger;
        &:hover {
            background-color: darken($messanger, 5%); }
        &:active {
            background-color: darken($messanger, 10%); }
        .icon {
            font-size: 24px; } } }

.btn-copy-ref {
    width: 100%;
    height: 40px;
    font-size: 15px;
    border-radius: 20px;
    background-color: transparent;
    border: 1px solid $extra-light-grey;
    font-weight: 600;
    letter-spacing: 0.02em;
    word-spacing: -0.04em; }
