.product-page {
    margin-bottom: 70px;
    &__main {
        position: relative;
        margin-top: 10px;
        margin-bottom: 66px;
        &_modal {
            margin-top: 40px;
            @include sm {
                margin-top: 60px; } }
        @include md {
            margin-bottom: 73px; }
        @include xs {
            margin-bottom: 42px;
            margin-top: 50px; }
        &_wright {}
        @include clr; }
    &__slider {
        float: left;
        width: 550px;
        @include md {
            width: 327px; }
        @include sm {
            float: none;
            width: calc(100% + 33px);
            margin-left: -11px;
            margin-right: -22px; } }
    &__details {
        float: left;
        width: calc(100% - 550px);
        padding-left: 40px;
        @include md {
            width: calc(100% - 327px);
            padding-left: 30px; }
        @include sm {
            float: none;
            padding-left: 0;
            width: 100%; } }
    &__close {
        display: block;
        position: absolute;
        right: -33px;
        top: 0;
        @include md {
            right: 0; }
        @include sm {
            top: -47px; } }
    &__title {
        font-size: 20px;
        line-height: 1.25;
        font-weight: 400;
        margin-bottom: 4px;
        // margin-top: 36px
        padding-right: 50px;
        @include md {
 }            // margin-top: 26px
        @include xs {
            // margin-top: 20px
            padding-right: 30px; } }
    &__price {
        font-weight: 700;
        font-size: 20px;
        margin-bottom: 24px;
        .rouble {
            font-size: 19px;
            font-weight: 500; } }
    &__info {
        margin-bottom: 22px;
        margin-top: 12px;
        // p
 }        //     margin-bottom: 2px
    &__date {
      margin-top: 4px; }
    &__size {
      margin-top: 10px; }
    &__buttons {
        margin-top: 29px;
        font-size: 0;
        & > * {
            margin-right: 10px;
            margin-bottom: 10px; } }
    &__descr {
        line-height: 1.35; }
    .btn {
        + .product-page__descr {
            margin-top: 26px; } }
    &__followers {
        margin-bottom: 24px; }
    &__user {
        margin-top: 26px;
        margin-bottom: 24px;
        @include xs {
          margin-top: 20px;
          margin-bottom: 16px; }
        // .user
        //     margin-bottom: 16px
        //     +xs
        //         align-items: flex-start
        //         margin-bottom: 6px
        //         .user__text
        //             max-width: 200px
        //         .user__main,
        //         .user__secondary
        //             white-space: pre-wrap
        //             display: inline
 }        //             padding-right: 0
    &__cards-title {
        font-size: 20px;
        font-weight: 600;
        line-height: 1.25;
        margin-bottom: 33px;
        text-align: left;
        @include xs {
            // max-width: 175px
            margin-bottom: 23px; } }
    &__cards {
        margin: 0 auto;
        text-align: center; }
    &__collections {
        width: 960px;
        display: inline-block;
        margin-bottom: 25px;
        @include md {
            width: 630px; }
        @include sm {
            width: 300px; } }
    &__collection {
        width: 300px;
        margin-bottom: 40px;
        // height: 300px
        &_narrow {
            @include r(900) {
                width: 266px; } }
        @include xs {
            margin-bottom: 26px; } } }


.views-details {
    color: $grey;
    font-size: 13px;
    line-height: 1.23; }
